.resume-hobbies-block .container {
  text-align: center;
}

.resume-hobbies-block-languages {
  display: inline-flex;
  flex-flow: column nowrap;
}

.resume-hobbies-block-languages:after {
  clear: both;
}

.resume-hobbies-block-language {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
  justify-content: space-between;
}

.resume-hobbies-block-languageTitleAndLevel {
  text-align: left;
  padding-right: 25px;
}

.resume-hobbies-block-languageTitle {
  text-transform: uppercase;
  font-weight: 500;
}

.resume-hobbies-block-languageLevel {
  color: #8a8a8b;
  width: 100%;
  text-align: left;
}

.resume-hobbies-block-hobbies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 50px;
}

.ResumeCustomersBlock-customer-container {
  display: inline-block;
  -webkit-perspective: 900px;
          perspective: 900px;
  width: 150px;
  height: 150px;
  margin: 10px;
}

.resume-hobbies-block-hobby {
  transition: all 0.6s ease;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.resume-hobbies-block-hobby-front,
.resume-hobbies-block-hobby-back {
  position: absolute;
  width: 150px;
  height: 150px;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.resume-hobbies-block-hobby-container:hover .resume-hobbies-block-hobby {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.resume-hobbies-block-hobby-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.resume-hobbies-block-hobby > div > div {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 16px !important;
  height: 100%;
}

.resume-hobbies-block-hobby {
  width: 150px;
  height: 150px;
  margin: 10px;
  text-align: center;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
}

.resume-hobbies-block-hobby h4 {
  margin: 4px;
}

.resume-hobbies-block-hobby .material-icons {
  font-size: 48px;
}

@media only screen and (max-width: 800px) {
  .resume-hobbies-block-hobbies {
    padding: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .resume-hobbies-block-language {
    flex-direction: column;
  }
}

@media only screen and (max-width: 480px) {
  .resume-hobbies-block-hobbies {
    padding: 20px;
  }
}

.BottomNavigation {
  z-index: 100;
  width: 100%;
  position: fixed;
  bottom: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.BottomNavigation-link {
  width: 100%;
  min-width: 60px;
  max-width: 168px;
  flex: 1 1;
  display: inline-flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: rgba(0, 0, 0, 0.54);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  cursor: pointer;
  outline: none;
  position: relative;
}

@media only screen and (max-width: 1170px) {
}

@media only screen and (max-width: 800px) {
  .BottomNavigation {
    flex-wrap: wrap;
  }

  .BottomNavigation-link {
    font-size: 0;
  }
}

@media only screen and (max-width: 480px) {
  .BottomNavigation {
    height: auto !important;
  }

  .BottomNavigation-link {
    font-size: 0;
    min-width: 25%;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

